'use client';


import Link from 'next/link';
import  CardLogin  from '@/app/(auth)/register/components/cardLogin'

export default function Register() {
    
    return (
        <div className="min-h-screen flex items-center justify-center bg-ccDarkGray">
            <div className="bg-white p-8 rounded-lg shadow-md w-full max-w-md">
                <h1 className="text-2xl font-bold text-center mb-6">Crear cuenta</h1>
                
                {error && (
                    <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
                        {error}
                    </div>
                )}
                
                <form onSubmit={handleSubmit}>
                    <div className="mb-4">
                        <label className="block text-gray-700 mb-2" htmlFor="name">
                            Nombre
                        </label>
                        <input
                            id="name"
                            name="name"
                            type="text"
                            className="w-full p-2 border rounded"
                            required
                            value={formData.name}
                            onChange={handleChange}
                        />
                    </div>
                    
                    <div className="mb-4">
                        <label className="block text-gray-700 mb-2" htmlFor="email">
                            Correo electrónico
                        </label>
                        <input
                            id="email"
                            name="email"
                            type="email"
                            className="w-full p-2 border rounded"
                            required
                            value={formData.email}
                            onChange={handleChange}
                        />
                    </div>
                    
                    <div className="mb-4">
                        <label className="block text-gray-700 mb-2" htmlFor="password">
                            Contraseña
                        </label>
                        <input
                            id="password"
                            name="password"
                            type="password"
                            className="w-full p-2 border rounded"
                            required
                            value={formData.password}
                            onChange={handleChange}
                        />
                    </div>
                    
                    <div className="mb-6">
                        <label className="block text-gray-700 mb-2" htmlFor="confirmPassword">
                            Confirmar contraseña
                        </label>
                        <input
                            id="confirmPassword"
                            name="confirmPassword"
                            type="password"
                            className="w-full p-2 border rounded"
                            required
                            value={formData.confirmPassword}
                            onChange={handleChange}
                        />
                    </div>
                    
                    <button
                        type="submit"
                        className="w-full bg-ccBlue text-white py-2 rounded-xl hover:bg-blue-700 transition"
                        disabled={loading}
                    >
                        {loading ? 'Registrando...' : 'Registrarse'}
                    </button>
                </form>
                
                <div className="mt-4 text-center">
                    <p>
                        ¿Ya tienes una cuenta?{' '}
                        <Link href="/auth/login" className="text-blue-600 hover:underline">
                            Iniciar sesión
                        </Link>
                    </p>
                </div>
            </div>
        </div>
    );
}